import React,{useState} from 'react'
import './portfolio.css'
import ilustration from '../../assets/image/onlineCV.svg'
import Detail from './detail'
export default function Portfolio() {
    const [menu, setMenu] = useState(['software','hardware'])
    const [active,setActive] = useState('software')

    const ButtonMenu = ()=> (
        menu.map(nama =>  <button className={`btn-project ${active === nama ? "active":""}`} onClick={()=>setActive(nama)}>{nama}</button>)
    )
    return (
        <div>
            <div className="section">
                <div className="jumbotron-portfolio fadein">
                    <img className="illustration" src={ilustration}></img>
                    <div className="right-box">
                        <div className="header-title">MY PORTFOLIO</div>
                        <p>As an IOT and Web Developer, let me share my experience about most highlighted work of mine.</p>
                        <p>I have defined my work into 2 types. Please choose one of them to see my recent work</p>
                        <div className="choose-container">
                            <ButtonMenu/>
                        </div>
                    </div>
                </div>
            </div>
            <Detail menu={active}/>
        </div>
    )
}
