import React from 'react'
import "react-responsive-carousel/lib/styles/carousel.min.css";
import { Carousel } from 'react-responsive-carousel'
import './showcase.css'

export default function Showcase(props) {
    const ImageContainer = (props) => {
        return (
            <div>
                <img className="img" alt="" src={props.url} height={(props.height != undefined)?props.height:null}/>
            </div>
        )
    }
    return (
        <Carousel autoPlay width={360} dynamicHeight={false} renderIndicator={false} infiniteLoop stopOnHover showStatus={false} interval={7000}>
            {
                props.images.map(image=>{
                    return <ImageContainer url={image} height={props.height}/>
                })
            }
        </Carousel>
    )
}
